.item{
  text-decoration: none;
  border-radius: 10px;
  padding: 2px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.itemTextLight{
  color: rgb(55, 56, 55);
}
.itemTextDark {
  color: #f0f6f0;
}